import React from 'react';
import { Link } from 'react-router-dom'

class Header extends React.Component {
    state = {
        headerScrolling: false,
    }
    componentDidMount() {
        window.addEventListener("scroll", () => {
            if (window.pageYOffset > 50) this.setState({ headerScrolling: true });
            else this.setState({ headerScrolling: false });
        })
    }
    render() {
        const { headerScrolling, } = this.state;
        return (
            <div className="Header-container header-container">
                <div className={`Header ${headerScrolling ? 'HeaderScrolled' : ''}`}>
                    <div className="HeaderGroup">
                        <Link to="/"aria-current="page" className="LogoGroup"><img className="LogoMark" src="/images/chair.svg" alt="logo" /><span className="LogoText">MRM Salon</span></Link>
                        <a href="tel:770-864-1351" className="ButtonGroup">
                            <button>Call Now</button>
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;
